	export default {
		data() {
			return {
				name:'',
				zj:'',
				wxh:'',
				tel:''
			}
		},
		onLoad() {
		   
		},
		methods: {
			back : function(){
			   uni.navigateBack({
			  
			   });
			},
			tiJiao:function(){
				if(
					this.name!='',
					this.zj!='',
					this.wxh!='',
					this.tel!=''
						){
					let that=this;
					let usurl=that.url1+"/dinghai/City/insertCity";
					uni.showLoading({
						title:'发送中..',mask: true
					})
					uni.request({
								url: usurl,
								header:{
									"Content-Type" : "application/x-www-form-urlencoded"
								},
								method:'POST',
								data: {
									cityname:that.name,
									tel:that.tel,
									wechat:that.wxh,
									content:that.zj
								},
						    success: (res) => {
								console.log(res)
								if(res.statusCode==200){
									uni.showToast({
										title:"已收到"
									})
									uni.navigateBack({
										
									})
								}else{
									uni.showToast({
										title:"您的网络打了个盹",
										icon:"none"
									})
								}
						    },
							fail : function(err){
								console.log(err)
								uni.showToast({
									title:"您的网络打了个盹",
									icon:"none"
								})
							}
						});
				}else{
					uni.showToast({
						title:'请完整填写',
						icon:'none'
					})
				}
			}
		 }
}
